body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

main {
  padding-top: 8%;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  width: 80%;
  margin: 0 auto;
  justify-content: space-evenly; }

@media (max-width: 920px) {
  main {
    flex-direction: column;
    align-items: center; } }

.navigation {
  width: 80%;
  max-width: 80%;
  margin: 0 auto;
  justify-content: center;
  padding-top: 1rem; }
  .navigation__list {
    display: flex;
    margin: 0;
    list-style: none;
    font-size: 0.8rem;
    align-items: flex-end;
    flex-wrap: wrap; }
  .navigation__item {
    position: relative;
    padding: 0.5rem; }
  .navigation__link {
    color: #fff;
    text-decoration: none;
    text-transform: uppercase;
    font-weight: 500; }
    .navigation__link--sale {
      position: absolute;
      color: #f2f701;
      text-transform: uppercase;
      font-weight: 500;
      font-size: 0.6rem;
      top: 0;
      left: 7%; }
  .navigation__link:hover {
    color: #f2f701; }
  .navigation__logo {
    margin-left: -40px;
    margin-right: auto; }

@media (max-width: 930px) {
  .navigation__list {
    padding: 0;
    align-items: center;
    flex-direction: column; }
  .navigation__logo {
    margin: 0 auto; } }

.countdown__container {
  width: 45%;
  color: #fff; }
  .countdown__container .btn {
    color: #fff;
    font-size: 0.8rem;
    width: 15rem;
    border-radius: 0.5rem;
    height: 3rem;
    background-color: #cf4064;
    text-transform: uppercase;
    font-weight: 600;
    border: none; }
    .countdown__container .btn:hover {
      cursor: pointer; }

.countdown__header {
  font-size: 1.4rem;
  font-weight: 500; }

.countdown__list {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  margin-bottom: 1rem;
  align-items: center; }
  .countdown__list li {
    text-align: center;
    position: relative;
    padding: 0.5rem;
    font-size: 1.5rem;
    font-weight: 600; }
    .countdown__list li span {
      font-size: 0.6rem;
      text-transform: uppercase;
      display: block; }
  .countdown__list .arrows {
    color: #cf4064;
    font-size: 0.8rem; }

.offers__container {
  width: 45%;
  color: #fff; }

.offers__offer {
  display: flex;
  flex: row 1 wrap;
  align-items: center;
  margin-top: -1rem;
  justify-content: flex-end;
  padding: 2rem; }
  .offers__offer--aside1 {
    font-weight: 700;
    font-size: 6rem; }
  .offers__offer--number {
    position: relative;
    margin: 0px 1rem;
    display: inline-block;
    font-weight: 700;
    font-size: 18rem; }
    .offers__offer--number span:first-child {
      position: absolute;
      font-size: 1rem;
      display: block;
      text-align: center;
      top: 18%;
      left: 50%;
      margin-top: 4px;
      -webkit-transform: translateX(-50%) translateY(-50%);
              transform: translateX(-50%) translateY(-50%);
      background-color: #cf5d7a;
      padding: 0.3rem; }
    .offers__offer--number span:nth-child(2) {
      font-size: 1rem;
      position: absolute;
      display: block;
      text-align: center;
      -webkit-transform: translateX(-50%) translateY(-50%);
              transform: translateX(-50%) translateY(-50%);
      top: 24%;
      left: 50%;
      color: #cf5d7a; }
  .offers__offer--aside2 {
    display: flex;
    flex-direction: column; }
    .offers__offer--aside2 :first-child {
      font-weight: 700;
      font-size: 4rem; }
    .offers__offer--aside2 :nth-child(2) {
      font-size: 3rem; }

@media (max-width: 920px) {
  .countdown__container {
    text-align: center;
    display: flex;
    width: 100%;
    flex-direction: column;
    align-items: center; }
  .offers__container {
    display: flex;
    justify-content: center; } }

.guarantee__container {
  display: flex;
  flex-direction: row;
  width: 80%;
  color: #fff;
  justify-content: center;
  align-items: center;
  margin: 2% auto 0 auto; }

.guarantee__list {
  display: flex;
  flex-direction: row;
  list-style: none; }

.guarantee__item {
  padding: 0 0.5rem; }
  .guarantee__item p {
    display: inline-block; }
  .guarantee__item span {
    color: #22aa4f; }

@media (max-width: 930px) {
  .guarantee__list {
    flex-direction: column; } }

.landing {
  min-height: 100vh;
  background: linear-gradient(rgba(20, 20, 20, 0.3), rgba(20, 20, 20, 0.3)), url(/task-frontend/static/media/background.7c0b8a4c.jpg);
  background-size: cover; }

