  padding-top: 8%;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  width: 80%;
  margin: 0 auto;
  justify-content: space-evenly; }

@media (max-width: 920px) {
  main {
    flex-direction: column;
    align-items: center; } }

